import lazyLoading from './lazyLoading'
import Apis from '../core/Apis'

export default {
    path: '/finance',
    meta: {
        icon: 'el-icon-document',
        label: '财务管理',
        hidden: false
    },
    component: require('../views/Home'),
    children: [
        {
            name: 'ToReview',
            path: 'to-review',
            component: lazyLoading('finance/ToReview'),
            meta: {
                label: '提现审核',
                hidden: false,
                api: [
                    Apis.qutuiba.convert.applyList
                ]
            }
        }, {
            name: 'RateChange',
            path: 'rate-change',
            component: lazyLoading('finance/RateChange'),
            meta: {
                label: '汇率调整',
                hidden: false,
                api: [
                    Apis.qutuiba.exchange.setExchangeByDate,
                    Apis.qutuiba.exchange.getYesterdayGather
                ]
            }
        }
    ]
}



