import BaseRequest from './BaseRequest';
import Apis from './Apis';
import Auth from './Auth';
import DataCenterLog from './DataCenterLog';
import ApiDomain from './ApiDomain';

class QuRequest extends BaseRequest {
    constructor() {
        super();
        this.Apis = Apis;
        this.apiDomain = new ApiDomain();
    }

    getUrl(api) {
        return this.apiDomain.getUrl(api);
    }

    nativeSend(api, data = {}){
        data = Object.assign({},data);
        data.token = Auth.getToken();
        data.project = this.apiDomain.getProject();

        return super.send({
            url: api.url,
            type: api.type,
            timeout: api.timeout,
            headers: api.headers
        }, data).then((response) => {
            let res = response.data;
            if (res.code == 0 || res.code == -2101) {
                return res;
            } else if (res.code == -126) {
                // 清除登录状态，避免登录状态失效后死循环问题
                Auth.delToken();
                setTimeout(() => {
                    location.href='/login'
                }, 2000);
                throw new Error('登陆状态失效, 请登陆');
            } else {
                DataCenterLog.send({
                    item: 2001, path: api.url, author: api.author, code: res.code, message: res.message
                });
                // TODO 想法：实现一个 ApiStack 类，负责在展示详细的错误日志( request, response, api, stack... )
                throw {
                    message: `请求失败code: ${res.code}, message: ${ res.message || '未知原因' }，接口开发者：${ api.author }`,
                    stack: new Error().stack,
                    code: res.code
                };
            }
        }, (error) => {
            let status = 0;
            if (error.response) {
                status = error.response.status;
            } else if (error.request) {
                status = error.request.status;
            }
            throw new Error(`网络错误 [ ${ status } ] ${ error.message } `);
        });
    }

    send(api, data = {}) {
        let url = this.apiDomain.getUrl(api);
        return this.nativeSend({
            url: url,
            type: api.type,
            timeout: api.timeout,
            headers: api.headers,
            author: api._author || ''
        } , data)
    }

    isProduction(){
        return this.apiDomain.isProduct();
    }

    getProject(){
        return this.apiDomain.getProject()
    }
}

export default QuRequest;



