import lazyLoading from './lazyLoading'
import Apis from '../core/Apis'

export default {
    path: '/global',
    meta: {
        icon: 'el-icon-setting',
        label: '全局配置',
        hidden: false
    },
    component: require('../views/Home'),
    children: [
        {
            name: 'GlobalSetting',
            path: 'global-setting',
            component: lazyLoading('global/GlobalSetting'),
            meta: {
                label: '全局配置',
                hidden: false,
                api: [
                    Apis.qutuiba.config.list
                ]
            }
        }, {
            name: 'Package',
            path: 'package',
            component: lazyLoading('global/Package'),
            meta: {
                label: '包管理',
                hidden: false,
                api: [
                    Apis.qutuiba.package.getList
                ]
            }
        }, {
            name: 'Version',
            path: 'version',
            component: lazyLoading('global/Version'),
            meta: {
                label: '版本管理',
                hidden: false,
                api: [
                    Apis.qutuiba.version.getList
                ]
            }
        }, {
            name: 'Cdn',
            path: 'cdn',
            component: lazyLoading('global/Cdn'),
            meta: {
                label: 'CDN',
                hidden: false,
                api: [
                    Apis.qutuiba.cdn.getCdnList
                ]
            }
        }, {
            name: 'Domain',
            path: 'domain',
            component: lazyLoading('global/Domain'),
            meta: {
                label: '收徒域名管理',
                hidden: false,
                api: [
                    Apis.qutuiba.domain.getList
                ]
            }
        }, {
            name: 'Upload',
            path: 'upload',
            component: lazyLoading('global/Upload'),
            meta: {
                label: '图片上传',
                hidden: false,
                api: [
                    Apis.qutuiba.upload.uploadFile
                ]
            }
        }
    ]
}



