import lazyLoading from './lazyLoading'
import Apis from '../core/Apis'

export default {
    path: '/operation',
    meta: {
        icon: 'fa fa-user-o',
        label: '用户管理',
        hidden: false
    },
    component: require('../views/Home'),
    children: [
        {
            name: 'User',
            path: 'user',
            component: lazyLoading('operation/user/User'),
            meta: {
                label: '用户管理',
                hidden: false,
                api: [
                    Apis.qutuiba.member.getMemberList
                ]
            }
        }, {
            name: 'PushMessage',
            path: 'push-message',
            component: lazyLoading('operation/PushMessage'),
            meta: {
                label: '系统消息',
                hidden: false,
                api: [
                    Apis.qutuiba.message.pushList
                ]
            }
        }, {
            name: 'Tool',
            path: 'tool',
            component: lazyLoading('operation/user/Tool'),
            meta: {
                label: '工具',
                hidden: true,
                api: [
                    Apis.qutuiba.exchange.setExchangeByDate,
                    Apis.qutuiba.exchange.getYesterdayGather,
                    Apis.qutuiba.member.changeBalance
                ]
            }
        }
    ]
}



